html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background-color: #191414;
}

h1 {
  font-size: 4rem;
  text-align: center;
  margin: 0;
}

h2 {
  font-size: 1.5rem;
}

p {
  font-size: 0.8rem;
}

nav {
  background-color: #191414;
  color: white;
  padding: 10px 0px;
}

.App {
  background-attachment: fixed;

  background-repeat: no-repeat;
  background-size: cover;
}

.login {
  display: grid;
  grid-template-areas: "a a" "c d";
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto 1fr;
  color: white;
  background-color: rgba(0, 0, 0, 0.7);
  font-size: 2rem;
  gap: 70px 0px;
  padding: 0px 0px 100px 0px;
  width: 100%;
  max-width: 1800px;
  margin: auto;
}

.a {
  grid-area: a;
  margin: 0px auto;
  width: 100%;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.8);
  color: #191414;
  padding: 0px 0px 100px 0px;
}

.a h1 {
  animation-name: dropIn;
  animation-delay: 0s;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.header-description {
  font-size: 3rem;
  font-weight: 600;
  width: 60%;
  margin: auto;
  padding-top: 120px;
  animation-name: dropIn;
  animation-delay: 0s;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.a-fade {
  animation-name: fadeIn;
  animation-delay: 0s;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

.a p {
  padding: 20px 0px;
}

.search-box {
  border-radius: 25px;
  padding: 20px;
}

.b {
  grid-area: b;
  margin: auto;
  width: 100%;
}

.c {
  grid-area: c;

  text-align: center;
  padding-top: 80px;
  height: 100%;
  width: 100%;
}

.d {
  grid-area: d;
  display: grid;
  grid-template-rows: 1fr auto;
  margin: auto;
  text-align: center;
  height: 100%;
  width: 100%;
}

.track-info {
  animation-name: fadeIn;
  animation-delay: 0s;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.track-data {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  max-width: 800px;
  margin: auto;
  animation-name: fadeIn;
  animation-delay: 0s;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.login-block {
  padding: 0px 0px 50px 0px;
}

.login-title {
  padding: 50px 0px;
  opacity: 0;
  animation-name: slideIn;
  animation-delay: 0.5s;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.login a {
  background-color: #1db9b1;
  color: white;
  cursor: pointer;
  border: none;
  border-radius: 25px;
  padding: 10px 30px 10px 30px;
  font-family: "Poppins", sans-serif;
  font-size: 1.5rem;
  width: 100%;
  transition: 0.5s;
  text-decoration: none;
}

.login a:hover {
  background-color: rgba(255, 255, 255, 0);
  color: #191414;
  border: 1px solid;
}

.login button {
  background-color: #1db9b1;
  color: white;
  cursor: pointer;
  border: none;
  border-radius: 25px;
  padding: 10px;
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
  width: 50%;
  transition: 0.5s;
}

.login button:hover {
  background-color: white;
  color: #191414;
}

.login button:focus {
  outline: none;
}

.login input {
  font-family: "Poppins", sans-serif;
  border: none;
  background-color: rgba(0, 0, 0, 0);
  color: white;
  border-bottom: 2px solid white;
  width: 80%;
  font-size: 1rem;
  margin-bottom: 20px;
}

.login input:focus {
  outline: none;
}

.key {
  font-size: 1rem;
}

.footer {
  padding: 50px 0px;
  margin: 0px 0px 0px 0px;
  text-align: center;
  color: white;
  background-color: #1db9b1;
}

@media screen and (max-width: 600px) {
  .App {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .header-description {
    font-size: 8vw;
    width: 100%;
    text-align: center;
    margin: auto;
    padding: 50px 0 20px 0px;
  }

  .login a {
    font-size: 1rem;
  }

  .login-title {
    padding: 20px 0px 50px 0px;
    width: 70%;
    margin: auto;
  }

  div {
    font-size: 4vw;
  }

  p {
    font-size: 2vw;
  }

  .login {
    display: grid;
    grid-template-areas: "a a" "d d" "c c";
    grid-template-columns: 1fr;
    grid-template-rows: 400px auto 1fr;
  }

  .login button {
    width: 80%;
  }

  .c {
    padding: 0px;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes dropIn {
  from {
    transform: translateY(-50px);
  }
  to {
    transform: translateY(0px);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}
